import react from 'react';
import './card.css';

export const Card =(props)=>{
    return (
        <div className="card-container">
            <img src={`https://robohash.org/${props.member.id}?set=set2`}>
            </img>
            <h2> {props.member.name} </h2>
            <p>  {props.member.email} </p>
        </div>
    )
} 