import logo from './logo.svg';
import './App.css';
import React from 'react';
import { CardList } from './component/card-list/cardlist.component';
import {SearchInput} from './component/searchItem/search.component';

class App extends React.Component {
  constructor(){
    super();
    this.state={
      members:[],
      serachField:''
    }
  }

  componentDidMount(){
    
    fetch('https://jsonplaceholder.typicode.com/users')
    .then(response => response.json())
    .then(data => this.setState({members:data}))
  }

  handleChange = (e)=>{
    this.setState( (prevState)=> ({ serachField: e.target.value }) , ()=>{console.log(this.state.serachField)} )
  }

  render(){
    let {serachField ,members} =this.state;
    console.log(members)
    const filteredArray = members.filter( (member) => member.name.toLowerCase().includes(serachField.toLowerCase()) )

    return(
      <div className="App">
          <h1>Members list</h1>
          <SearchInput placeholder="search member" handleChange={this.handleChange} />
          <CardList members={filteredArray}/>
      </div>
    )
  }

}

export default App;
